/**
 * 搜索列表翻页
 * 定义一个最多显示条目（比如
 * Created by cyrilluce on 2016/8/14.
 */
"use strict";

import React, { Component, PropTypes } from 'react'

export default class SearchPagingation extends Component {
    render() {
        let {page=1, size=20, count=0} = this.props;
        let pages = [];
        let maxPage = Math.ceil(count/size);

        if(maxPage<=0){
            return <div></div>;
        }

        for(let i=1; i<=maxPage; i++){
            let curPage = i;
            pages.push(
                <li key={curPage} className={curPage===page ? 'active' : ''} onClick={e=>curPage!==page && this.pageTo(curPage)}><a href="#">{curPage}</a></li>
            );
        }
        pages.push(
            <li key="next" className={page===maxPage ? 'disabled' : ''} onClick={e=>curPage!==maxPage && this.pageTo(page+1)}>
                <a href="#" aria-label="Next">
                    <span aria-hidden="true">&raquo;</span>
                </a>
            </li>
        );

        return (
            <nav aria-label="Page navigation">
                <ul className="pagination">{pages}</ul>
            </nav>
        )
    }

    pageTo(page){
        this.props.onPaging(page);
    }
}

SearchPagingation.propTypes = {
    page : PropTypes.number.isRequired,
    size : PropTypes.number,
    count : PropTypes.number,
    onPaging: PropTypes.func.isRequired
};


