"use strict";
import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import SearchBar from '../components/SearchBar';
import SearchList from '../components/SearchList';
import SearchPagingation from '../components/SearchPagingation';
import {search, paging} from '../../redux';

class ItemDb extends Component {
    render() {
        // Injected by connect() call:
        const { dispatch } = this.props;
        let props = this.props;
        return (
            <div>
                <SearchBar keyword={props.keyword} onSearch={keyword=>dispatch(search(keyword))} />
                <SearchList searching={props.result.loading} list={props.result.data.list || []} />
                <SearchPagingation onPaging={page=>dispatch(paging(page))} page={props.page} count={props.result.data.total} size={props.size} />
            </div>
        )
    }
}

// 包装 component ，注入 dispatch 和 state 到其默认的 connect(select)(App) 中；
export default connect(state=>state)(ItemDb)


