import React from 'react';
import { render } from 'react-dom'
import getStore from "../redux";
import getRoot from '../react';

// 创建新的 Redux store 实例
const store = getStore(window.__INITIAL_STATE__);

render(getRoot(store), document.getElementById('root'));


